<template>
  <center>
    <div>
      <table border="1" cellspacing="1" cellpadding="1">
        <tr>
          <th width="200" bgcolor="#ffeded">제목</th>
          <th width="100" bgcolor="#ffeded">글쓴이</th>
          <th width="150" bgcolor="#ffeded">날짜</th>
          <th width="100" bgcolor="#ffeded">조회수</th>
        </tr>
        <tr v-for="qnaItem in qnalist" v-bind:key="qnaItem.num">
          <td align="center">
            <span
              v-show="qnaItem.comment != null"
              style="font-weight:bold; color:blue; font-size:5px"
            >[답변완료]</span>
            <span style="text-align:center;" @click="getQna(qnaItem.num)">{{qnaItem.title}}</span>&nbsp;
            <span
              v-show="qnaItem.wdate.substring(0, 10) == new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate()"
              style="color:red; font-size:3px"
            >
              <img src="resources/img/new.jpg" alt="Image" class="img-fluid" />
            </span>
          </td>

          <td align="center">{{qnaItem.name}}</td>

          <td align="center">{{qnaItem.wdate.substring(0, 10)}}</td>

          <td align="center">{{qnaItem.count}}</td>
        </tr>
      </table>
    </div>
  </center>
</template>

<script>
import Constant from "../Constant";

export default {
  created() {
    this.$store.dispatch(Constant.GET_QNALIST); //action call
  },
  computed: {
    //store안의 qnalist check
    qnalist() {
      return this.$store.state.qnalist;
    }
  },
  methods: {
    removeQna: function(num) {
      this.$store.dispatch(Constant.REMOVE_QNA, { num });
    },
    getQna: function(num) {
      this.$router.push("/detail/" + num);
    }
  }
};
</script>

