<script>
import Constant from "../Constant";
export default {
  created() {
    //call getTodo with param
    return this.$route.params.qna;
  },
  computed: {
    qna() {
      return this.$store.state.qna;
    }
  },
  methods: {
    updateQna() {
      this.$store.dispatch(Constant.UPDATE_QNA, { qna: this.qna }); //insert
      this.$router.push("/list"); //목록 화면으로 넘어감
      this.clear();
    },
    clear() {
      this.todo = {};
    }
  }
};
</script>

