<template>
  <div>
    <table border="1" cellspacing="1" cellpadding="1">
      <thead>
        <tr>
          <th width="100" bgcolor="#ffeded">번호</th>
          <th width="200" bgcolor="#ffeded">내용</th>
          <th width="100" bgcolor="#ffeded">작성날짜</th>
          <th width="150" bgcolor="#ffeded">작성자</th>
          <th width="150" bgcolor="#ffeded">댓글</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <!-- <td> -->
          <td align="center">{{ qna.num }}</td>
          <td align="center">{{ qna.content }}</td>
          <td align="center">{{ qna.wdate }}</td>
          <td align="center">{{ qna.name }}</td>
          <td align="center">{{ qna.comment }}</td>
        </tr>
      </tbody>
    </table>
    <button @click="updateQna(qna)">질문 수정</button>
    <br />
    <br />
    <input type="text" v-model="qna.comment" placeholder="댓글 입력" size="42" />
    <button @click="addComment()" style="font-family: 'Sunflower', sans-serif; " >댓글 입력</button>
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  created() {
    //call getTodo with param
    this.getQna(this.$route.params.num);
    this.getId(this.$store.dispatch(Constant.GET_ID));
  },
  computed: {
    qna() {
      return this.$store.state.qna;
    }
  },
  methods: {
    getId(){
      this.$store.dispatch(Constant.GET_ID);
    },
    getQna(num) {
      //action call
      this.$store.dispatch(Constant.GET_QNA, { num: num });
    },
    addComment() {
      this.$store.dispatch(Constant.ADD_COMMENT, { qna: this.qna });
    },

    updateQna(qna) {
      this.$router.push("/update/" + qna);
    },

    clear() {
      this.todo = {};
    }
  }
};
</script>

